<template>
<br>
<div class="edit">
  <div class="edit_frame">
    <p class="font_xl"><b></b></p>
    <div v-for="(qInfo, index) in this.questionList" :key="index" style="border:2px solid #808080; border-radius:10px; padding: 5px 0px; margin-bottom:10px">
      <div class="container-fluid">
        <div class="row">
          <div class="col-5" style="display:flex;">
            <span class="font_l"><b>{{ index + 1 }}</b></span>
          </div>
          <div class="col-7" style="text-align:right">
              <span v-show="index != 0" v-on:click="pushRowBtn('UP', index)" style="width:60px; margin-right:10px" class="link"> 1</span>
              <span v-show="index != this.questionList.length - 1" v-on:click="pushRowBtn('DOWN', index)" style="width:60px; margin-right:10px" class="link"> 1</span>
          </div>
        </div>
      </div>
      <div class="sp_line_s" />
      <div class="container-fluid">
        <div>
          <p class="font_m" style="text-align:left;"></p>
          <textarea id="story" name="story" v-model="this.questionList[index]['quiz_body']" rows="5" cols="33" style="width:100%" class="fc"></textarea>
          <div style="text-align:right">
              <span class="link" v-on:click="pushRowBtn('TEMPLATE', index)"> </span>
              <span class="link" v-on:click="pushRowBtn('RANDOM', index)" style="margin-left:10px"> </span>
          </div>
          
          <p class="font_m" style="text-align:left;"></p>
          <input type="text" v-model="this.questionList[index]['choices1']" class="form-control" aria-label="Text input with radio button">
          
          <div class="sp_line_m" />

          <p class="font_m" style="text-align:left;"> 1</p>
          <input type="text" v-model="this.questionList[index]['choices2']" class="form-control" aria-label="Text input with radio button">
          <div class="sp_line_ss" />
          <input type="text" v-model="this.questionList[index]['choices3']" class="form-control" aria-label="Text input with radio button">
          <div class="sp_line_ss" />
          <input type="text" v-model="this.questionList[index]['choices4']" class="form-control" aria-label="Text input with radio button">
          <p class="font_s"></p>

          <div style="text-align:right">
            <span v-on:click="pushRowBtn('DELETE', index)" class="link_red" :src="this.$root.IMG_PATH + 'btn_delete.png'" style="width:60px"> </span>
          </div>
        </div>
        <p class="errMsg" v-show="this.questionList[index]['errMsg'] != ''">{{ this.questionList[index]['errMsg'] }}</p>
      </div>
    </div>
    <p class="errMsg" v-show="this.errMsg['questions'] != ''">{{ this.errMsg['questions'] }}</p>
    <div class="sp_line_m" />
    <span v-show="this.questionList.length < 30" class="btnBase" style="display:inline-block;width:100%;text-align:center" v-on:click="pushAddQuestion"></span>
  </div>

  <div class="sp_line_xl" />

  <div class="edit_frame">
    <p class="font_xl"><b></b></p>
    <p class="errMsg" v-show="this.errMsg['icon'] != ''">{{ this.errMsg['icon'] }}</p>
    <img class="icon" :src="iconImgUrl" style="width:90px;">
    {{iconFileName}}
    <input type="file" @change="onFileChange" />
    <div class="sp_line_s" />
    <span class="btnBase" style="display:inline-block;width:100%;text-align:center" v-on:click.prevent.stop="pushIcon(0)"></span>
      <div class="sp_line_m" />
    <span class="btnBase" style="display:inline-block;width:100%;text-align:center" v-on:click.prevent.stop="pushIcon(1)">Twitter</span>
  </div>

  <div class="sp_line_xl" />

  <div class="edit_frame">
    <p class="font_xl"><b></b></p>
    <p class="errMsg" v-show="this.errMsg['title'] != ''">{{ this.errMsg['title'] }}</p>

    <p class="font_l"></p>
    <div class="sp_line_s" />
    <input type="text" class="fc" v-model="title" style="width:100%"><br>

    <div class="sp_line_m" />

    <p class="font_l"> </p>
    <div class="sp_line_s" />
    <input type="text" class="fc" v-model="subTitle" style="width:100%" placeholder=""><br>  

    <div class="sp_line_m" />

    <p class="font_m"></p>
    <div class="sp_line_s" />
    <p class="font_m">{{ titleExample }}</p>    
  </div>

  <div class="sp_line_xl" />

  <div class="edit_frame">
    <p class="font_xl"><b></b></p>
    <p class="errMsg" v-show="this.errMsg['description'] != ''">{{ this.errMsg['description'] }}</p>
    <p class="font_m"></p>
    <div class="sp_line_s" />
    <textarea id="story" name="story" v-model="description" rows="4" style="width:100%"></textarea>
  </div>


  <div class="sp_line_xl" />

  <div class="edit_frame">
    <p class="font_xl"><b></b></p>
    <p class="errMsg" v-show="this.errMsg['tags'] != ''">{{ this.errMsg['tags'] }}</p>
    <p class="font_m">5</p>
    <div class="sp_line_s" />
    <input type="text" v-model="tags[0]" style="width:50%">
    <div class="sp_line_s" />
    <input type="text" v-model="tags[1]" style="width:50%">
    <div class="sp_line_s" />
    <input type="text" v-model="tags[2]" style="width:50%">
    <div class="sp_line_s" />
    <input type="text" v-model="tags[3]" style="width:50%">
    <div class="sp_line_s" />
    <input type="text" v-model="tags[4]" style="width:50%">
  </div>

  <div class="sp_line_xl" />

  <div class="edit_frame">
    <p class="font_xl"><b>SNS</b></p>
    <p class="errMsg" v-show="this.errMsg['url3'] != ''">{{ this.errMsg['url3'] }}</p>
    <p class="font_m">SNSHP</p>
    <div class="sp_line_s" />
    <div class="input-group mb-3">
      <span class="input-group-text fc" id="basic-addon3">URL1</span>
      <input type="text" v-model="url1" class="form-control" id="basic-url" aria-describedby="basic-addon3">
    </div>
    <p class="errMsg" v-show="this.errMsg['url1'] != ''">{{ this.errMsg['url1'] }}</p>

    <div class="input-group mb-3">
      <span class="input-group-text fc" id="basic-addon3">URL2</span>
      <input type="text" v-model="url2" class="form-control" id="basic-url" aria-describedby="basic-addon3">
    </div>
    <p class="errMsg" v-show="this.errMsg['url2'] != ''">{{ this.errMsg['url2'] }}</p>

    <div class="input-group mb-3">
      <span class="input-group-text fc" id="basic-addon3">URL3</span>
      <input type="text" v-model="url3" class="form-control" id="basic-url" aria-describedby="basic-addon3">
    </div>
  </div>

  <div class="sp_line_xl" />

  <div class="edit_frame">
    <p class="font_xl"><b></b></p>
    <p class="errMsg" v-show="this.errMsg['publishing'] != ''">{{ this.errMsg['publishing'] }}</p>
    <div style="text-align:left">
      <div class="form-check">
        <input class="form-check-input" v-model="publishing" value="1" type="radio" name="publishing1" id="publishing1">
        <label class="form-check-label font_m" for="publishing1">
          
        </label>
      </div>
      <div class="sp_line_s" />
      <div class="form-check">
        <input class="form-check-input" v-model="publishing" value="2" type="radio" name="publishing2" id="publishing2">
        <label class="form-check-label font_m" for="publishing2">
          URL
        </label>
      </div>
      <div class="sp_line_s" />
      <div class="form-check">
        <input class="form-check-input" v-model="publishing" value="0" type="radio" name="publishing0" id="publishing0" checked>
        <label class="form-check-label font_m" for="publishing0">
          
        </label>
      </div>
      <br>
    </div>
  </div>

  <div class="sp_line_xl" />

  <div class="edit_frame">
    <p class="font_xl"><b></b></p>
    <p class="font_m"><br> </p>
    <div class="sp_line_s" />
    <div style="text-align:left">
      <div class="form-check">
        <input class="form-check-input" v-model="delScore" value="0" type="radio" name="flexRadioDefault" id="flexRadioDefault1">
        <label class="form-check-label font_m" for="flexRadioDefault1">
          
        </label>
      </div>
      <div class="sp_line_s" />
      <div class="form-check">
        <input class="form-check-input" v-model="delScore" value="1" type="radio" name="flexRadioDefault" id="flexRadioDefault2">
        <label class="form-check-label font_m" for="flexRadioDefault2">
          
        </label>
      </div>
      <br>
    </div>
  </div>

  <div class="sp_line_xl" />

  <div style="text-align:center">
    <button type="button" class="btnBase" style="width:200px" v-on:click="pushSave"></button>
  </div>

  <div class="sp_line_m" />

  <div style="text-align:center">
    <button type="button" class="btnBase" style="width:200px" v-on:click="pushBack"></button>
  </div>
  <br>
</div>
<popup-window ref="popup" @pushTemp="pushTemp" ></popup-window>

<div id="cardBody" :style="'position:absolute; width:800px; height:418px; left:1500px; top:20px; border: 1px solid; background-image: url(' + this.$root.IMG_PATH + 'card/bg_card.png); background-repeat:repeat'">
  <img class="icon" :src="iconImgUrl" style="position:absolute; left:30px; top:130px; width:250px;">
  <p :style="'position:absolute; left:0px; margin:auto; transform:translate(0%, -50%); top:60px; font-size:' + cardTitleFont + 'px; width:100%'"><b>{{ title }}</b></p>
  <p style="position:absolute; right:30px; top:130px; font-size:50px"><b></b></p>
  <p style="position:absolute; right:30px; bottom:20px; font-size:90px"><b>{{ questionList.length }}</b></p>
  <!-- <div>
    <p style="position:absolute; right:120px; bottom:20px; font-size:90px"><b>{{ questionList.length }}00</b></p>
    <p style="position:absolute; right:20px; bottom:45px; font-size:30px"><b></b></p>
  </div> -->
</div>
</template>


<script>
import axios from 'axios'

// this.$root.reloadValue = new Date().getTime();

export default {
  name: 'Edit',
  components: {
  },
  data() {
    return {
      cardStr : "",
      userId: "",
      quizId: "",
      title: "",
      subTitle: "",
      description: "",
      tags : ["","","","",""],
      url1: "",
      url2: "",
      url3: "",
      publishing : 0,
      delScore : 0,

      iconFileName : "",
      iconImgUrl : "",
      iconFile : null,

      questionList: [],
      selTempIndex: 0,

      errMsg : {
        icon : "",
        questions : "",
        title : "",
        description : "",
        tags : "",
        url1 : "",
        url2 : "",
        url3 : "",
        publishing : "",
      },

      lengths : {
        qBody : 200,
        qChoice : 50,
        title : 30,
        description : 100,
        tag : 10,
        url : 200,
      }
    };
  },
  computed : {
    cardTitleFont : function () {
      let val = 700 / this.title.length;
      console.log("font " + val);
      if (val > 80) {
        val = 80;
      }
      return val;
    },
    titleExample : function() {
      let retVal = this.title + "";
      if (this.subTitle != "") {
        retVal = retVal + "" + this.subTitle + "";
      }
      return retVal;
    }
  },
  mounted () {
    window.addEventListener('scroll', () => {
        this.show = (window.scrollY > 150);
    });
  },
  methods : {
    test : function () {
      console.log(JSON.stringify(this.questionList));
    },
    onFileChange(e) {
      this.errMsg.icon = "";
      const files = e.target.files || e.dataTransfer.files;
      let size = files[0].size;
      let type = files[0].type;

      //
      if (size > 1024000) {
        //
        this.errMsg.icon = "1MB";
        return;
      }
      if (type != "image/jpeg" && type != "image/png") {
        //
        this.errMsg.icon = "pngjpeg";
        return;
      }
      console.log(size + " " + type);

      this.createImage(files[0]);
      this.iconFile = files[0];
      this.iconFileName = files[0].name;
    },
    createImage(file) {
      const reader = new FileReader();
      reader.onload = e => {
        this.iconImgUrl = e.target.result;
      };
      reader.readAsDataURL(file);
    },
    pushIcon : function(val) {
      this.$root.reloadValue = new Date().getTime();
      if (val == 0) {
        //
        this.iconFileName = "";
        this.iconImgUrl = this.$root.ICON_PATH + "quiz_" + this.quizId + ".jpg?" + this.$root.reloadValue;
      } else if (val == 1) {
        //Twitter
        this.iconFileName = "TWITTER";
        this.iconImgUrl = this.$root.ICON_PATH + "user_" + this.userId + ".jpg?" + this.$root.reloadValue
      }
      this.iconFile = null;
    },
    pushBack : function () {
      this.$router.push("/");
    },
    pushSave : function () {
      console.log("pushSave");
      //
      this.title = this.title.trim();
      this.description = this.description.trim();
      this.tags[0] = this.tags[0].trim();
      this.tags[1] = this.tags[1].trim();
      this.tags[2] = this.tags[2].trim();
      this.tags[3] = this.tags[3].trim();
      this.tags[4] = this.tags[4].trim();
      this.url1 = this.url1.replace(' ', '');
      this.url2 = this.url2.replace(' ', '');
      this.url3 = this.url3.replace(' ', '');

      for (let index = 0; index < this.questionList.length; index++) {
        let qData = this.questionList[index];
        qData["quiz_body"] = qData["quiz_body"].trim();
        qData["choices1"] = qData["choices1"].trim();
        qData["choices2"] = qData["choices2"].trim();
        qData["choices3"] = qData["choices3"].trim();
        qData["choices4"] = qData["choices4"].trim();
      }

      let checkFlg = true;
      for (let index = 0; index < this.errMsg.length; index++) {
        this.errMsg[index] = "";
      }
      for (let index = 0; index < this.questionList.length; index++) {
        this.questionList["errMsg"] = "";
      }

      //
      if (this.publishing < 0 || this.publishing > 2) {
        //
        this.errMsg["publishing"] = "";
        checkFlg = false;
      }
      if (this.publishing != 0) {
        //
        // 
        if (this.title == "") {
          //
          this.errMsg["title"] = "";
          checkFlg = false;
        }

        // 
        if (this.description == "") {
          //
          this.errMsg["description"] = "";
          checkFlg = false;
        }

        //
        if (this.questionList.length == 0) {
          //
          this.errMsg["questions"] = "";
          checkFlg = false;
        }
      }

      // 
      if (this.title.length > this.lengths["title"]) {
        //
        this.errMsg["title"] = "" + this.lengths["title"] + "";
        checkFlg = false;
      }
      // 
      if (this.title.length > this.lengths["description"]) {
        //
        this.errMsg["description"] = "" + this.lengths["description"] + "";
        checkFlg = false;
      }

      // 
      for (let index = 0; index < this.tags.length; index++) {
        if (this.tags[index].length > this.lengths["tag"]) {
          //
          this.errMsg["tags"] = "" + this.lengths["tag"] + "";
          checkFlg = false;
        }
      }

      //URL1 
      if (this.url1.length > this.lengths["url"]) {
        //
        this.errMsg["url1"] = "URL" + this.lengths["url"] + "";
        checkFlg = false;
      }

      //URL2 
      if (this.url2.length > this.lengths["url"]) {
        //
        this.errMsg["url2"] = "URL" + this.lengths["url"] + "";
        checkFlg = false;
      }

      //URL3 
      if (this.url3.length > this.lengths["url"]) {
        //
        this.errMsg["url3"] = "URL" + this.lengths["url"] + "";
        checkFlg = false;
      }
        // this.errMsg[""] = "";
        // checkFlg = false;

      //
      for (let index = 0; index < this.questionList.length; index++) {
        let qData = this.questionList[index];

        //
        let trimFlg = true;
        while (trimFlg) {
          trimFlg = false;
          if (qData.choices3 == "" && qData.choices4 != "") {
            qData.choices3 = qData.choices4;
            qData.choices4 = "";
            trimFlg = true;
          }
          if (qData.choices2 == "" && qData.choices3 != "") {
            qData.choices2 = qData.choices3;
            qData.choices3 = "";
            trimFlg = true;
          }
        }

        //
        if (this.publishing != 0) {
          //
          if (qData.choices1 == "") {
            //
            qData["errMsg"] = "";
            checkFlg = false;
          }
          
          // 
          if (qData.quiz_body == "") {
            //
            qData["errMsg"] = "";
            checkFlg = false;
          }

          // 2
          if (qData.choices2 == "") {
            //
            qData["errMsg"] = "1";
            checkFlg = false;
          }
        }

        // 
        if (qData.quiz_body.length > this.lengths["qBody"]) {
          //
          qData["errMsg"] = "" + this.lengths["qBody"] + "";
          checkFlg = false;
        }

        // 
        if ((qData.choices1.length > this.lengths["qChoice"]) ||
          (qData.choices2.length > this.lengths["qChoice"]) ||
          (qData.choices3.length > this.lengths["qChoice"]) ||
          (qData.choices4.length > this.lengths["qChoice"])) {
          //
          qData["errMsg"] = "" + this.lengths["qChoice"] + "";
          checkFlg = false;
        }
      } 

      if (!checkFlg) {
        return;
      }

      if (this.publishing != 0) {
        //
        window.html2canvas(document.getElementById('cardBody'), {
          scale:1
        }).then(canvas => {
          console.log(canvas.width + " " + canvas.height);
          //canvas
          // let url = canvas.toDataURL("png");
          // let link = document.getElementById("downloadLink");
          // link.href= url;
          // link.download = "_" + this.quizData["quiz_id"] + ".png";
          var base64 = canvas.toDataURL('image/jpeg');
          base64 = base64.replace(/^.*,/, '');
          this.cardStr = base64;
          console.log(this.cardStr);

          this.updateData();
        });
      } else {
        //
        this.cardStr = "";
        this.updateData();
      }
    },
    updateData : function () {
      console.log(this.subTitle);
      //
      console.log(this.description);
      let params = new FormData();
      params.append('card', this.cardStr);
      params.append('iconFile', this.iconFile);
      params.append('iconFileName', this.iconFileName);
      params.append('quiz_id', this.quizId);
      params.append('quiz_name', this.title);
      params.append('quiz_sub_name', this.subTitle);
      params.append('description', this.description);
      params.append('questions', this.questionList.length);
      params.append('url1', this.url1);
      params.append('url2', this.url2);
      params.append('url3', this.url3);
      params.append('publishing', this.publishing);
      params.append('tag1', this.tags[0]);
      params.append('tag2', this.tags[1]);
      params.append('tag3', this.tags[2]);
      params.append('tag4', this.tags[3]);
      params.append('tag5', this.tags[4]);
      params.append('questionData', JSON.stringify(this.questionList));
      params.append('deleteScore', this.delScore);
      
      let config = {
          headers: {'content-type': 'multipart/form-data'}
      };

      console.log("IN");
      axios.post(this.$root.SERVER_PATH + "updateMyData", params, config)
      .then((response) => {
        // let resData = response.data;
        if (!this.$root.checkServerResponse(response)) {
          return;
        }

        if (this.publishing == 0) {
          this.$refs.popup.body = "";
        } else if (this.publishing == 1) {
          this.$refs.popup.body = "\r\n";
        } else if (this.publishing == 2) {
          this.$refs.popup.body = "\r\n";
        }
        this.$refs.popup.showModal("UPDATE");
        this.$root.reloadImgValue();
      });
    },
    pushRowBtn : function (kbn, index) {
      console.log(kbn + " - " + index);
      if (kbn == "UP") {
        let taihi = this.questionList[index - 1];
        this.questionList[index - 1] = this.questionList[index];
        this.questionList[index] = taihi;
      } else if (kbn == "DOWN") {
        let taihi = this.questionList[index + 1];
        this.questionList[index + 1] = this.questionList[index];
        this.questionList[index] = taihi;
      } else if (kbn == "DELETE") {
        this.questionList.splice(index, 1);
      } else if (kbn == "TEMPLATE") {
        this.selTempIndex = index;
        this.$refs["popup"].showModal("CATEGORY");
      } else if (kbn == "RANDOM") {
        let tempList = this.$refs.popup.modalAllTemplateList;
        let ranIndex = Math.floor(Math.random() * tempList.length);
        this.questionList[index]["quiz_body"] = tempList[ranIndex]["temp_body"];
      }
      console.log(this.questionList);
    },
    pushAddQuestion : function () {
      console.log("IN");
      let question = {
        quiz_body : "",
        choices1 : "",
        choices2 : "",
        choices3 : "",
        choices4 : "",
      };
      this.questionList.push(question);
    },
    pushTemp : function (tempBody) {
      this.questionList[this.selTempIndex]["quiz_body"] = tempBody;
    },
    initAction : function () {
      console.log(this.$route.params.quizId);
      
      let params = new URLSearchParams();
      params.append("quizId", this.$route.params.quizId);
      axios.post(this.$root.SERVER_PATH + "getEditData", params)
      .then((response) => {
        let resData = response.data;
        console.log(resData);

        if (!this.$root.checkServerResponse(response)) {
          return;
        }

        let baseQuizData = resData["baseQuizData"];
        this.userId = baseQuizData["user_id"];
        this.iconImgUrl = this.$root.ICON_PATH + "quiz_" + this.$route.params.quizId + ".jpg";

        this.questionList = resData["questionList"];
        this.$refs.popup.modalAllTemplateList = resData["tempList"];

        this.quizId = baseQuizData["quiz_id"];
        console.log(this.quizId);
        this.title = baseQuizData["quiz_name"];
        this.subTitle = baseQuizData["quiz_sub_name"];
        this.description = baseQuizData["description"];
        if (baseQuizData["tag1"] == null) {
          this.tags[0] = "";
        } else {
          this.tags[0] = baseQuizData["tag1"];
        }

        if (baseQuizData["tag2"] == null) {
          this.tags[1] = "";
        } else {
          this.tags[1] = baseQuizData["tag2"];
        }
        if (baseQuizData["tag3"] == null) {
          this.tags[2] = "";
        } else {
          this.tags[2] = baseQuizData["tag3"];
        }
        if (baseQuizData["tag4"] == null) {
          this.tags[3] = "";
        } else {
          this.tags[3] = baseQuizData["tag4"];
        }
        if (baseQuizData["tag5"] == null) {
          this.tags[4] = "";
        } else {
          this.tags[4] = baseQuizData["tag5"];
        }
        this.url1 = baseQuizData["url1"];
        this.url2 = baseQuizData["url2"];
        this.url3 = baseQuizData["url3"];

        this.publishing = baseQuizData["publishing"];
        this.delScore = 0;
        console.log(this.questionList);
      })
      .catch((e) => {
        alert(e); 
      });
    },
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.initAction();
    });
  },
  beforeRouteUpdate(to, from, next) {
    next(vm => {
      vm.initAction();
    });
  },
}
</script>

